@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@1,700&family=Yellowtail&display=swap);
.Card {
    border: 1px solid black;
    border-radius: 5px;
    padding: 16px;
    background-color: #999;
    width: 180px;
    height: 260px;
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.5em;
}

.Card:hover {
    background-color: #666;
}

.card-container {
    margin-left: 20px;
    margin-bottom: 30px;
}

@media screen and (max-width: 576px) {
    .card-container {
        margin: 0 auto;
        margin-bottom: 30px;
    }
}

.Card h6 {
    color: black;
    text-decoration: none;
    text-align: center;
    font-weight: lighter;
}
.home {
    padding: 0 10vh;
}

.search-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 20px;
    margin-top: 20px;
}

.search {
    margin: auto;
    width: 50%;
    height: 30px;
    border: 2px blue solid;
    border-radius: 2rem;
    font-size: 20px;
    text-align: center;
    min-width: 350px;
}

#admin {
    height: 36px;
    padding-top: 10px;
    padding-right: 1rem;
    padding-left: 1rem;
    border-radius: 1rem;
    color: #555555;
}

#admin:hover {
    background-color: #f5f5f5;
    color: #222222;
}

/* @media screen and (min-width: 768px){
    .search {
        max-width: 500px;
        margin-bottom: auto;
    }
} */

.list {
    display: flex;
    /* padding-left: 10vh; */
    flex-wrap: wrap;
}

a {
    text-decoration: none;
}

.songsList-container {
    background-color: rgba(256, 0, 0, 0.5);
}
.h1-pd20{
    padding-top: 2px;
    padding-left: 20px;
    font-family: 'Ubuntu', sans-serif;
}
.artistsList-container {
    background-color: rgba(0, 256, 0, 0.5);
}
.albumsList-container {
    background-color: rgba(0, 0, 256, 0.5);
}

/* 
==========================
        RESPONSIVE
========================== */

@media screen and (max-width: 576px) {
    .home {
        padding: 0 3vh;
    }

}
.player {
    position: fixed;
    bottom: 0;
    height: 10vh;
    width: 100vh;
    margin-left: 50vh;
}

.player audio {
    margin-left: auto;
}
.songContainer {
    width: 90%;
    margin: 0 auto;
    max-width: 450px;
    min-width: 250px;
    /* height: 30px; */
    border-radius: 2rem;
    background-color: rgba(69, 69, 69, 0.5);
}

.songContainer h4 {
    padding: 1rem 4rem;
}

.songContainer:hover {
    cursor: pointer;
    background-color: rgba(150, 150, 150, 0.5);
}

.title-container {
    height: 200px;
    background-color: rgba(0, 0, 256, 0.75);
}

.title-container h1{
    margin-left: 50px;
    margin-top: 0;
    padding: 50px 0;
    font-size: 4rem;
    /* text-align: center; */
    letter-spacing: 125%;
    color: whitesmoke;
}

.album-nav {
    height: 30px;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.album-nav-link {
    width: 30%;
    text-align: center;    
}

.album-nav-link a{
    text-decoration: none;
}
.form {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.form-element {
    width: 50%;
    margin-top: 30px;
    text-align: center;
}

.form input {
    border: 2px solid blue;
    background-color: whitesmoke;
    border-radius: 20px;
    height: 60px;
    font-size: 2rem;
}
.form button {
    width: 30%;
    border: 2px solid green;
    background-color: whitesmoke;
    border-radius: 20px;
    height: 60px;
    font-size: 2rem;
}

.form button:hover {
    cursor: pointer;
    background-color: darkgray;
    color: green;
    font-weight: bold;
}

@media screen and (max-width: 576px){
    .form-element{
        width: 80%;
    }
}


a {
    text-decoration: none;
}
